.App {
  text-align: center;
  background-color: #29323c;
}

.App-logo {
  height: 80px;
}

.App-header {
  background-color: #222;
  height: auto;
  padding: 20px;
  color: #7f8081;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.5);
}

.App-intro {
  font-size: large;
}

.screening-data {
  display: flex;
  flex-direction: row;
  flex-flow: wrap;
  margin-bottom: 2em;
}

.App-header .virus {
  display: inline-block;
  width: 50px;
  animation: App-logo-spin infinite 50s linear;
}

.chart {
  position: relative;
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  transition: all 1s;
}

.grid-2 {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 1em;
  margin: 1em;
}

.chart .chart-inner {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 400px;
  width: 100%;
  margin: 0;
  padding: 0;
  border-radius: 0.5em;
  background-color: #222;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
}

.chart.loading {
  -webkit-filter: blur(10px);
  -moz-filter: blur(10px);
  -o-filter: blur(10px);
  -ms-filter: blur(10px);
  filter: blur(10px);
}

.chart #loading-text {
  position: absolute;
  z-index: 99999;
  top: 50%;
  left: 40%;
  font-size: 1.5em;
  letter-spacing: 2px;
  color: antiquewhite;
}

.screening-data .btn {
  padding: 14px 28px;
  background-color: #29323c;
  color: whitesmoke;
  border-radius: 1em;
  width: 100px;
  height: 100px;
  box-shadow: inset 0px 3px 15px rgba(0, 0, 0, 0.3);
  margin: 1em auto;
}

.screening-data .title {
  font-size: 10px;
  font-weight: bolder;
  margin: 1em auto;
  width: 100%;
  color: goldenrod;
}

.screening-data .text {
  color: gold;
  font-weight: bolder;
  text-align: center;
}

/* .screening-data .btn:hover { 
  box-shadow: inset 0px 3px 15px rgba(0,0,0,0.2);
} */

.screening-data .btn img {
  margin: auto;
  width: 30px;
  height: 30px;
}

.map-container {
  margin: 1em;
  border-radius: 1em;
  background-color: #222;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
  height: 500px;
  overflow: hidden;
}

.paper-title {
  display: inline-block;
  background-color: #222;
  border: 1px solid #29323c;
  color: #7f8081;
  padding: 0.3em;
  border-radius: 0.3em;
  font-size: 3vw;
  text-align: center;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
}

.districts-row {
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 0 auto;
}

.districts-row .district {
  width: auto;
  background-color: #29323c;
  padding: 0.5em;
  margin: auto;
  border-radius: 0.3em;
  color: #7f8081;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
  transition: all 0.5s;
  font-size: 1vw;
  cursor: pointer;
}

.districts-row .district:hover {
  color: #222;
  background-color: burlywood;
  border: 1px solid #222;
  transform: scale(1.1);
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.5);
  transition: all 0.5s;
}
.map-data-col {
  height: 100%;
}

.affected-areas {
  position: relative;
  border: 1px solid #29323c;
  margin: 1em;
  height: 35vh;
  overflow-y: scroll !important;
}

.affected-areas .district-area {
  overflow: hidden;
  margin: 1em;
  padding: 0.5em;
  border-radius: 0.3em;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.1);
  background-color: #29323c;
  cursor: pointer;
}

.container {
  position: relative;
  margin: 1em;
  height: 80vh;
}

.container .chart .chart-inner {
  height: 100%;
}

.map-container .map-container-inner {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 1em;
  height: 100%;
}

#mapid {
  height: 500px;
  width: 100%;
  margin: 0;
  padding: 0;
  bottom: 0;
}

.footer {
  justify-content: center;
  text-align: center;
  width: 100%;
  height: auto;
  background-color: #222;
}

.footer p {
  margin: auto;
  color: whitesmoke;
  font-size: 1em;
  font-weight: bold;
  padding: 2em;
}

.footer a {
  text-decoration: none;
  color: rgb(11, 160, 206);
}

.contributors {
  width: 100%;
  font-size: 0.7em;
  color: aqua;
  padding-bottom: 2em;
  margin: 0;
}

.contributors img {
  height: 3em;
  width: 3em;
  margin: 0.1em;
  border-radius: 50%;
  border: 2px solid #29323c;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.5);
  transition: all 0.5s;
}

.contributors img:hover {
  transform: scale(2);
  border: #29323c;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.9);
  transition: all 0.5s;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media only screen and (max-width: 800px) {
  .grid-2 {
    display: block;
    grid-template-columns: 1fr;
  }

  .chart {
    margin-bottom: 1em;
  }

  .chart .chart-inner {
    height: 500px;
  }
  .map-container {
    height: 98vh;
  }

  .affected-areas {
    max-height: 200px;
    height: 100%;
  }

  .affected-areas .district-area {
    padding: 2px;
    margin: 5px;
    font-size: 10px;
  }

  .districts-row .district {
    font-size: 2.5vw;
  }

  .map-container .map-container-inner {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr;
  }
}
